<template>
    <div>
        <navbar />
        <div style="text-align: center" class="main-container">

            <div v-if="status == 'success'">
                <img src="../../assets/images/status-success.png"/>
                <h2>激活成功</h2>
            </div>
            <div v-if="status == 'error'">
                <img src="../../assets/images/status-error.png"/>
                <h2>{{errorMsg}}</h2>
            </div>
        </div>
    </div>
</template>

<script>
    import Navbar from "../../components/Navbar";
    import {userActiveApi} from "../../requests/user";
    export default {
        name: "Active",
        components: {Navbar},
        data(){
            return {
                status:"",
                errorMsg:""
            }
        },
        mounted(){
            userActiveApi({
                token: this.$route.params.token
            }).then(response=>{
                this.status = "success"
            }).catch(err=>{
                this.status = "error"
                this.errorMsg = err.response.data.msg
            })
        }
    }
</script>

